import React, {useEffect, useState} from "react";
import "../styles/darcula.css";
import "../style/typewriter.css"
import hljs from "highlight.js"

const typingSpeed = 50;
export default function TypeWriter(props) {
    // Track what the current substring being displayed is
    const [state, setState] = useState("");

    // Start typeWriter loop
    
    useEffect(() =>
        {
            let curr = 0;
            let timer: NodeJS.Timeout;
            function getCurrText() {
                curr++;
                // not ideal with jsx elements since hljs removes its own styling elems while the next elem is being typed
                return hljs.highlight("javascript", props.message.substring(0, curr)).value;
            }
            function startTyping() {
                setState(getCurrText());
                timer = setTimeout(startTyping, typingSpeed);
            }
            startTyping();

            return () => {clearTimeout(timer)};

            // note props.message does not update. This is just a componentDidMount
            // added here since it's a dependency for the array
        }, [props.message]
    );


    return(
        <div id={"mainPageCode"}>
            <pre><code dangerouslySetInnerHTML={{__html: state}}/></pre>
        </div>
    );
}