import React, {Component} from "react";
import {BrowserRouter as Router, Route, Switch, Redirect} from "react-router-dom";
import NotFound from "./views/notFound";
import ResumePage from "./views/ResumePage"
import third_page from "./components/third_page";
import NotesPage from "./components/notes";

export default class WebsiteRouter extends Component{
    render(){
        return(
            <Router basename={process.env.PUBLIC_URL}>
                <Switch>
                    <Route exact path = {"/"} component={ResumePage}/>
                    <Route path={"/404"} component={NotFound}/>
                    <Route path={process.env.PUBLIC_URL + "/hi"} component={third_page}/>
                    <Route path={"/ho"} component={third_page}/>
                    <Route path={"/hop/"} component={third_page}/>
                    <Route path={"/home/hu"} component={third_page}/>
                    <Route path={"/notes"} component={NotesPage}/>
                    <Redirect to={"/404"}/>
                </Switch>
            </Router>
        )
    }
}