import React from "react";
import "../style/first_page.css"
import TypeWriter from "./HighlightCode";

export default function FirstPage() {
    return (
        <div id={"first_page"}>
            <div id={"myNameParent"}>
                <div id={"myName"}> Toshi Tabata </div>
            </div>
            <TypeWriter message={
`import React, {useEffect, useState} from "react";
import "../styles/darcula.css";
import "../style/typewriter.css"
import Highlight from "./highlight";

const typingSpeed = 100;
export default function TypeWriter(props) {
    const [state, setState] = useState("");
    // Start typeWriter loop
    useEffect(() => {
            let timer:any = "";
            function startTyping() {
                setState(currState => (
                    getCurrText(currState)
                ));
                timer = setTimeout(startTyping, typingSpeed)
            }
            startTyping();
            return () => clearTimeout(timer);
        }, []
    );

    function getCurrText(state) {
        return props.message.substring(0, state.length + 1);
    }
    return(
        <div>
            <pre>{state}</pre>
        </div>
    );
}`
            }>
            </TypeWriter>
        </div>
    )
}